<template>
	<div id='editor' class="warp">

		<!--头部导航 start-->
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{path:'/shop/shopinfo'}">店铺信息</el-breadcrumb-item>
				<el-breadcrumb-item>编辑基础信息</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<!--头部导航 end-->
		
		<!--编辑基础信息 start-->
		<section class="basicinformation">
			<div class="title">
				编辑基础信息
			</div>
			<div class="info-warp">
				<el-form ref="form" label-width="90px" :model='form'>
					<el-form-item label="店铺名称" class='shopname'>
						<el-input @change="clearErrTip" v-model='form.shop_name' :class="{errtip: isShowErrtip}"></el-input>
						<span class="errMsg reds" v-if='shopnameErr'>请输入中文或字母</span>
					</el-form-item>
					<el-form-item label="店铺Logo">
						<div class="shoplogo left" v-if="!shoplogo">
							<img src="../../img/shop_icon.png" />
						</div>
						<div class="shoplogo left" v-else>
							<div class="bg"></div>
							<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+shoplogo" alt="店铺图片" />
							<big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
							<i class="el-icon-view" @click='clickImg'></i>
						</div>
						<img-upload @send-image='getPhoto' :BtnText='btnText'  :ImageSize='imgSize'></img-upload>
						<div class="text-warp left">
							<p class="text">一个月内只能更换一次，文件类型必须为JPG、JPEG、PNG，宽500像素，高500像素，文件大小不得超过1M</p>
						</div>
					</el-form-item>
						<el-form-item label="店铺电话" class='area'>
						<el-input @change="clearErrTip" v-model='form.telephone' :class="{errtip: isShowErrtip}"></el-input>
						<span class="err reds" v-if='shopPhone'>请输入正确格式的信息</span>
					</el-form-item>
					<el-form-item label="店铺面积" class='area'>
						<el-input @change="clearErrTip" v-model='form.shop_area' :class="{errtip: isShowErrtip}"></el-input>
						<span class="pinfan">m²</span>
						<span class="err reds" v-if='shopArea'>请输入数字</span>
					</el-form-item>
					<el-form-item class='rel' label="店铺地址">
						<el-input @change="clearErrTip" v-model='form.address'></el-input>
						<span class="ardess" @click="openMap"></span>
					</el-form-item>
					<el-form-item label="营业模式">
						<el-button class='resmo' disabled>{{busType[shopinfo.shop_model]}}</el-button>
					</el-form-item>
					<div class="bottom">
						<button ref='btn' class="defau" type="button" :class="{blue: showBtn}" @click="onSubmit()">保存</button>
						<button class="default" type="button" @click="goBack">取消</button>
					</div>
				</el-form>
			</div>
		</section>
		<!--编辑基础信息 end-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->

    <!-- 地图 -->
    <map-component 
    v-if="isShowMap" 
    @on-close="closeMap"
    @get-address="getFormAddress"></map-component>
	</div>
</template>

<script>
import { saveShopinfo, getShopinfo } from "../../../api";
import { Util } from "@/config/util";
import { Public } from "@/config/public";
import { businessType } from "@/config/cfg";
import {
  Confimshoparea,
  Character,
  Address,
  ConfimPhone
} from "../../../index";

import MapComponent from "@/components/baidumap/index";
import ImgUpload from "@/components/upload/SUpload";
import BigImg from "../../../components/showBigImg";

export default {
  components: {
    ImgUpload,
    BigImg,
    MapComponent
  },
  data() {
    return {
      form: {
        shop_name: "", //店铺 名字
        telephone: "", //店铺电话
        shop_area: "", //店铺面积
        address: "", //店铺地址
        address_num: "" //店铺门牌号
      },
      shopinfo: {}, //用来接收请求过来的对象
      shoplogo: "", //保存店铺标志
      imgbase_url: imgbase_url, //图片路径
      imgSize: 1024, //上传图片尺寸上限
      btnText: "更换", //上传按钮文字显示
      imgSrc: "", //图片地址
      showImg: false, //显示大图
      busType: "", //营业模式
      msg: "", //提示框提示内容
      showTips: false, //控制显示Tips提示框
      shopnameErr: false, //店铺名称错误的提示信息
      shopArea: false, //店铺面积错误的提示信息
      shopPhone: false, //电话号码错误的提示信息
      isShowErrtip: false, //错误的样式
      showBtn: false, //按钮加上类的样式

      isShowMap: false //地图
    };
  },
  created() {
    this.getData();
  },
  computed: {
    businessType() {
      return businessType;
    }
  },
  methods: {
    /*保存店铺信息*/
    onSubmit() {
      let phone = this.form.telephone.replace(/(^\s*)|(\s*$)/g, "");
      if (!ConfimPhone(phone)) {
        this.isShowErrtip = true;
        this.shopPhone = true;
        return;
      }
      if (!Character(this.form.shop_name)) {
        this.isShowErrtip = true;
        this.shopnameErr = true;
        return;
      }
      if (!Confimshoparea(this.form.shop_area)) {
        this.isShowErrtip = true;
        this.shopArea = true;
        return;
      }
      let shopid = Public.getShopid();
      let data = {
        shopinfo_save: 1,
        shop_id: shopid,
        shop_name: this.form.shop_name,
        shop_area: this.form.shop_area,
        telephone: this.form.telephone,
        address: this.form.address,
        shop_logo: this.shoplogo
      };
      saveShopinfo(data, this.alts);
    },
    //保存后的回调
    alts(resp) {
      if (resp.ret === 0) {
        this.showTips = true;
        this.msg = "成功保存店铺信息";
        this.$store.commit("GETSTSHOPLOGO", this.shoplogo);
        this.$store.commit("GETSTSHOPNAME", this.form.shop_name);
        setTimeout(() => {
          this.$router.go(-1);
        }, 1500);
      } else {
        this.showTips = true;
        this.msg = "保存失败";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
      }
    },
    /*保存店铺信息*/
    //跳转到店铺信息
    goBack() {
      this.$router.go(-1);
    },
    //获取数据
    getData() {
      let _this = this;
      getShopinfo(
        {
          get_shopinfo_base: 1
        },
        function(resp) {
          if (resp) {
            _this.shopinfo = resp.data.shopinfo || {};
            _this.form.shop_name = _this.shopinfo.shop_name;
            _this.form.telephone = _this.shopinfo.telephone;
            _this.form.shop_area = _this.shopinfo.shop_area;
            _this.form.address = _this.shopinfo.address;
            _this.busType = businessType.code;
            if (!Util.isEmpty(_this.shopinfo.shop_logo)) {
              _this.shoplogo = _this.shopinfo.shop_logo;
            }
            _this.clearErrTip();
          }
        }
      );
    },
    //获取上传的的图片
    getPhoto(res) {
      this.shoplogo = res;
    },
    //点击显示大图
    clickImg() {
      this.showImg = true;
      // 获取当前图片地址
      this.imgSrc = this.shoplogo;
    },
    //关闭大图
    viewImg() {
      this.showImg = false;
    },
    //清除错误的样式
    clearErrTip() {
      if (
        this.form.shop_name != "" &&
        this.form.shop_area != "" &&
        this.form.telephone !== "" &&
        this.form.address !== ""
      ) {
        this.showBtn = true;
        let btn = this.$refs.btn;
        btn.disabled = false;
      } else {
        this.showBtn = false;
        let btn = this.$refs.btn;
        btn.disabled = true;
      }
      this.isShowErrtip = false;
      this.shopnameErr = false;
      this.shopArea = false;
      this.shopPhone = false;
    },

    //map
    openMap() {
      this.isShowMap = true;
    },
    closeMap() {
      this.isShowMap = false;
    },
    getFormAddress(resp) {
      this.form.address = resp;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.warp {
  position: relative;
  .nvs {
    background: #eff3fa;
    height: 26px;
  }
  background: #fff;
  .basicinformation {
    .title {
      @include fc($sizes, $grey);
      height: 40px;
      line-height: 40px;
      background: #f6f8fc;
      text-indent: 14px;
    }
    .reds {
      @include fc(12px, #E7487E);
    }
    .shopname {
      position: relative;
      .errMsg {
        position: absolute;
        left: 290px;
        top: 0;
      }
    }
    .info-warp {
      margin: 40px 0 0 148px;
      .shopTitle {
        @include fc(16px, #333333);
        font-weight: bold;
        margin-bottom: 18px;
        width: 90px;
        text-align: right;
      }
      .shoplogo {
        @include wh(100px, 100px);
        background: #e1e1e1;
        img {
          width: 100%;
          height: 100%;
          display: block;
        }
      }
      .shoplogo:hover .bg {
        position: absolute;
        left: 0px;
        top: 0px;
        @include wh(100px, 100px);
        background: rgba(0, 0, 0, 0.5);
      }
      .shopInfo {
        .shops {
          @include fc(14px, #000000);
          margin-left: 48px;
          margin-bottom: 20px;
          .shopNumber {
            padding-left: 14px;
          }
        }
      }
      .area {
        position: relative;
        .pinfan {
          position: absolute;
          left: 290px;
          top: 0;
        }
        .err {
          position: absolute;
          left: 320px;
          top: 0;
        }
      }
      .picInfo {
        text-indent: 0;
        padding-left: 20px;
      }
      .text-warp {
        .text {
          position: absolute;
          left: 122px;
          top: 22px;
          width: 280px;
        }
      }
      .text {
        margin-top: 20px;
        @include fc(12px, #9b9b9b);
        line-height: 16px;
        white-space: inherit;
      }
      .rel {
        position: relative;
        .ardess {
          position: absolute;
          left: 310px;
          top: 0;
          @include wh(32px, 32px);
          background: url(../../img/arrdess.png)no-repeat;
          cursor: pointer;
        }
      }
      button {
        cursor: pointer;
      }
      .bottom {
        width: 80%;
        text-align: center;
        margin-top: 140px;
        margin-left: 40px;
        margin-bottom: 20px;
        .defau {
          @include fc(16px, #ffffff);
          background: #d8d8d8;
          @include wh(160px, 40px);
          line-height: 40px;
          border-radius: 6px;
        }
        .default {
          @include fc(14px, #4877e7);
          background: #ffffff;
          @include wh(160px, 40px);
          line-height: 40px;
          border-radius: 6px;
          border: 1px solid #4877e7;
          margin-left: 50px;
        }
      }
    }
  }
  .blue {
    @include fc(16px, #ffffff);
    background: #4877e7 !important;
    @include wh(160px, 40px);
    line-height: 40px;
    border-radius: 6px;
  }
  .tips {
    position: fixed;
    @include wh(360px, 100px);
    background: #000;
    opacity: 0.8;
    left: 50%;
    top: 50%;
    margin-left: -180px;
    margin-top: -50px;
    text-align: center;
    border-radius: 2px;
    .info {
      display: inline-block;
      @include fc(14px, #fff);
      padding-top: 40px;
      letter-spacing: 0.5;
    }
  }
}
</style>
<style lang="scss">
@import "src/styles/mixin.scss";
#editor {
  .el-form {
    overflow: hidden;
  }
  .el-button {
    @include wh(60px, 30px);
    background: #ffffff;
    @include fc(14px, #4877e7);
    line-height: 30px;
    border-radius: 0;
    padding: 0;
  }
  .el-input__inner {
    border: 1px solid #d9d9d9;
    border-radius: 0;
    @include wh(280px, 34px);
  }
  .el-form-item__label {
    padding: 11px 0;
  }
  .el-form-item {
    position: relative;
    margin-bottom: 20px;
    &:before {
      content: "*";
      left: 22px;
      top: 14px;
      position: absolute;
      display: inline-block;
      @include wh(10px, 10px);
      color: #e7487e;
    }
    .el-form-item__content {
      margin-left: 104px !important;
    }
  }
  .el-breadcrumb__item__inner,
  .el-breadcrumb__item__inner a {
    color: #333;
    font-size: 12px;
  }
  .el-breadcrumb__separator {
    margin: 0 5px;
    color: #333;
  }
  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
    color: #4877e7;
  }
  .upload-btn {
    margin-left: 20px;
  }
  .img-view .img img {
    width: 600px;
    height: 400px;
    display: block;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -300px;
    margin-top: -200px;
    z-index: 1001;
  }
  .el-icon-view {
    position: relative;
    &:before {
      position: absolute;
      opacity: 0;
      left: 40px;
      top: -80px;
      font-size: 20px;
      color: #fff;
      cursor: pointer;
    }
  }
  .shoplogo:hover .el-icon-view {
    position: relative;
    &:before {
      position: absolute;
      opacity: 1;
      left: 40px;
      top: -80px;
      font-size: 20px;
      color: #fff;
      cursor: pointer;
    }
  }
  .resmo {
    cursor: default;
    @include wh(80px, 30px);
    border: 1px solid #f2f2f2;
    background: #f2f2f2;
    border-radius: 2px;
    @include fc(14px, #333);
  }
  .errtip {
    .el-input-group__prepend {
      border-color: red;
    }
    input {
      border-color: red;
    }
    input.el-input__inner:hover {
      border-color: red;
    }
    input.el-input__inner:focus {
      border-color: red;
    }
  }
}
</style>


