<template>
        <div class="baidu-map">
            <!-- 遮罩层 -->
            <div class="layer" @click="closeDialog"></div>
            <div class="content">
                <baidu-map 
                class="bm-view" 
                :ak="appkey"  
                center="深圳"
                :zoom="15" 
                :scroll-wheel-zoom="true"
                @ready="mapMounted"
                @click="singleclick">
                    <bm-view class="bm-map"></bm-view>
                    <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
                    <bm-scale anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-scale>
                    <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
                    <bm-marker :position="{lng: p.lng, lat:p.lat}" :dragging="true">
                    </bm-marker>
                </baidu-map>
            </div>
        </div>
</template>
<script>
const APP_KEY = "Pg92ix77dHU1SKioZ5y1Pea0Mdahlnve";

import {
  BaiduMap,
  BmView,
  BmGeolocation,
  BmScale,
  BmNavigation,
  BmMarker
} from "vue-baidu-map";

export default {
  components: {
    BaiduMap,
    BmView,
    BmGeolocation,
    BmScale,
    BmNavigation,
    BmMarker
  },

  data() {
    return {
      appkey: APP_KEY,
      p: {
        lng: 113.952228,
        lat: 22.532768
      },
      address: ""
    };
  },
  methods: {
    closeDialog() {
      this.$emit("on-close");
    },
    //移动气泡位置
    singleclick(e) {
      this.p.lng = e.point.lng;
      this.p.lat = e.point.lat;
    },
    //绑定事件读取地址
    mapMounted(e) {
      let map = e.map;
      let geoc = new e.BMap.Geocoder();
      let _this = this;

      map.addEventListener("click", e => {
        let pt = e.point;

        geoc.getLocation(pt, rs => {
          let addComp = rs.addressComponents;
          _this.address =
            addComp.province +
            addComp.city +
            addComp.district +
            addComp.street +
            addComp.streetNumber;
        });

        _this.$emit("get-address", _this.address);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.baidu-map {
  position: fixed;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 8000;
}

/*遮罩层样式*/
.baidu-map .layer {
  position: fixed;
  z-index: 8001;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: all 0.5s;
}

/*不限制图片大小，实现居中*/
.baidu-map .content {
  width: 80%;
  height: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 8002;
}
.bm-view {
  width: 100%;
  height: 100%;
}
.bm-map {
  width: 100%;
  height: 100%;
}
</style>


